import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import { HeaderButton, List, SimpleCell, RichCell, Cell, Separator, Link, platform, CellButton, Header, InfoRow, Counter, PanelHeaderButton, getElementById, Alert, IOS, Title, Text } from '@vkontakte/vkui';
import Icon28InfoOutline from '@vkontakte/icons/dist/28/info_outline';
import Icon28BrainOutline from '@vkontakte/icons/dist/28/brain_outline';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28GameOutline from '@vkontakte/icons/dist/28/game_outline';
import Icon28FavoriteOutline from '@vkontakte/icons/dist/28/favorite_outline';
import Icon28NameTagOutline from '@vkontakte/icons/dist/28/name_tag_outline';
import Icon28More from '@vkontakte/icons/dist/28/more';
import Icon28UserCircleOutline from '@vkontakte/icons/dist/28/user_circle_outline';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Icon24ChevronCompactRight from '@vkontakte/icons/dist/24/chevron_compact_right';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon28MoneyCircleOutline from '@vkontakte/icons/dist/28/money_circle_outline';
import Icon28MarketOutline from '@vkontakte/icons/dist/28/market_outline';
import Icon28HelpOutline from '@vkontakte/icons/dist/28/help_outline';

import './Intro.css';
import shablon from '../img/shablon.png';
import './Tochka.css';



const Home = ({ id, go, fetchedUser }) => (
	<Panel id={id} centered={false}>
	<PanelHeader left={<PanelHeaderButton onClick={go} data-to="home">
				{Home === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
		>
			–ü—Ä–æ—Ñ–∏–ª—å
		</PanelHeader>
		
      <RichCell
          disabled
          before={fetchedUser.photo_200 ? <Avatar size={85} src={fetchedUser.photo_200}/> : null}
        >
          {`${fetchedUser.first_name} ${fetchedUser.last_name}`}
        </RichCell> 



		
		   <Group>
      <SimpleCell before={<Icon28MarketOutline />} onClick={go} data-to="shop">–ú–∞–≥–∞–∑–∏–Ω</SimpleCell>
      <SimpleCell before={<Icon28MoneyCircleOutline />} onClick={go} data-to="sendmoney">–í—ã–≤–æ–¥ —Å—Ä–µ–¥—Å—Ç–≤</SimpleCell>
      <SimpleCell before={<Icon28HelpOutline />} onClick={go} data-to="info">–ü–æ–º–æ—â—å</SimpleCell>
      <Separator style={{ margin: '12px 0' }} />
      <SimpleCell before={<Icon28InfoOutline />} onClick={go} data-to="infoapp">–û –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–∏</SimpleCell>
    </Group>
	</Panel>

);

	

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
